import Vue from 'vue'
import Router from 'vue-router'
import paidOrderList from '../components/paidOrder/paidOrderList.vue'
import unpaidOrderList from '../components/unpaidOrder/unpaidOrderList.vue'
import me from '../components/me/me.vue'
import login from '../components/me/login.vue'
import orderEdit from '../components/paidOrder/orderEdit.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'paidOrderList',
      component: paidOrderList
    },
    {
      path: '/paidOrderList',
      name: 'paidOrderList',
      component: paidOrderList
    },
    {
      path: '/unpaidOrderList',
      name: 'unpaidOrderList',
      component: unpaidOrderList
    },
    {
      path: '/orderEdit',
      name: 'orderEdit',
      component: orderEdit
    },
    {
      path: '/me',
      name: 'me',
      component: me
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }
  ]
})



