// import QS from 'qs'
class BaseRequest {
  constructor (params) {
    if (!params) {
      params = {}
    }
    this.params = params
  }
  requestUrl () {
  }
  async start (successCallback, failCallBack) {
    // axios网络请求
    // this._self.$ajax.post(this.requestUrl(), QS.stringify(this.params)).then(successCallback, failCallBack)
    this._self.$ajax({
      method: 'post',
      url: this.requestUrl(),
      params: this.params
    }).then(successCallback, failCallBack)
  }
  setSelf (self) {
    this._self = self
    return this
  }
}
export default BaseRequest



