  import topTitle from '../common/topTitle.vue'
  import ordersTitle from '../common/ordersTitle.vue'
  import orderItem from '../common/orderItem.vue'
  import {OrderEdit, GetSysCodeValue} from '../../net/orderEdit/OrderEditApi'
  import {GetOrders} from '../../net/orderEdit/GetOrders'
  import {Login} from '../../net/me/loginApi'
  export default {
    name: 'paidList',
    components: {
      topTitle,
      ordersTitle,
      orderItem
    },
    data () {
      let self = this
      return {
        isOdd: false,
        topTitleProps: {
          name: '已收货款'
        },
        ordersTitleProps: {
          itemCount: '122',
          filterCallback: function () {
            console.log('filterCallback')
            if (!self.isOdd) {
              let params = {
                user_name: 'guojinjin',
                user_password: '1',
                user_language: 'ZHS',
                is_ipad: 'N'
              }
              new Login(params).setSelf(self).start(function (response) {
                console.log(response.data)
              }, function (response) {
                // 这里是处理错误的回调
                console.log(response)
              })
              self.isOdd = true
            } else {
              let params = {
                pagesize: '1',
                pagenum: '1',
                _fetchall: 'false'
              }
              new GetOrders(params).setSelf(self).start(function (response) {
                console.log(response.data)
              }, function (response) {
                // 这里是处理错误的回调
                console.log(response)
              })
            }
          },
          sortCallback: function () {
            console.log('sortCallback')
            if (!self.isOdd) {
              let params = {
                code: 'YWX_EXPRESS_STATE'
              }
              new GetSysCodeValue(params).setSelf(self).start(function (response) {
                console.log(response.data.result)
              }, function (response) {
                // 这里是处理错误的回调
                console.log(response)
              })
            } else {
              let params = {
                order_id: '-1',
                order_category: '1',
                order_state: '1',
                order_type: '1',
                receive_type: '1',
                weixin_operator_num: '000001',
                input_time: '20170711 23:15',
                customer_name: '吴SB',
                customer_phone: '13838384438',
                province: '江西省',
                city: '萍乡市',
                customer_address: '芦溪县吴SB家',
                express_type: '帅快递',
                result_ds: {
                  product_id: '12345678',
                  order_quantity: '1'
                }
              }
              new OrderEdit(params).setSelf(self).start(function (response) {
                console.log(response)
              }, function (response) {
                // 这里是处理错误的回调
                console.log(response)
              })
            }
          }
        },
        orderProps: {
          createTime1: '20170710 14:11',
          creater: '吴xxx',
          createTime2: '20170710 14:18'
        }
      }
    },
    methods: {
      handleClick () {
        this.$router.push({
          path: '/orderEdit'
        })
//        this.$router.push({path: '/orderEdit'})
      }
    }
  }



