
import { Tabbar, TabbarItem } from 'vux/src/components/tabbar'
export default {
name: 'appTab',
data () {
return {
tabs: ['已收货款', '待收货款', '个人中心'],
selected: '待收货款'
}
},
components: {
Tabbar, TabbarItem
},
mounted () {
if (this.$route.name === 'paidOrderList') {
this.selected = '已收货款'
} else if (this.$route.name === 'unpaidOrderList') {
this.selected = '待收货款'
} else if (this.$route.name === 'login') {
this.selected = '个人中心'
}
},
methods: {
onItemClick (index) {
if (index === 0) {
this.$router.replace({name: 'paidOrderList'})
this.selected = '已收货款'
} else if (index === 1) {
this.$router.replace({name: 'unpaidOrderList'})
this.selected = '待收货款'
} else if (index === 2) {
this.$router.replace({name: 'login'})
this.selected = '个人中心'
}
}
}
}



