export default {
  name: 'topTitle',
  props: {
    titleProps: {
      name: String,
      leftBtnVisible: {
        type: Boolean,
        default: false
      },
      leftBtnCallback: Function,
      rightBtnVisible: {
        type: Boolean,
        default: false
      },
      rightBtnCallback: Function
    }
  },
  methods: {
    handleBtnClick (callback) {
      if (!callback) {
        return
      }
      return callback()
    }
  }
}



