export default {
  name: 'ordersTitle',
  props: {
    titleProps: {
      itemCount: Number,
      filterCallback: Function,
      sortCallback: Function
    }
  },
  methods: {
    handleClick (callback) {
      if (!callback) {
        return
      }
      return callback()
    }
  }
}



